<template>
    <section>
        <div class="frame-login">
            <div class="logo"></div>
            <div class="container">
                欢迎用户登录
                <div class="frame-input">
                    <div class="name">密码：</div>
                    <div class="put"><el-input v-model="input" placeholder="请输入密码" show-password @keyup.enter.native="access()"></el-input></div>
                    <div class="landing" @click="access">登录</div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: 'login',
    data () {
        return {
            lod: 0,
            input: ''
        }
    },
    mounted () {
        // let islod = sessionStorage.getItem('spaceMenu')
        // if (islod === undefined || islod === null) {
        //     this.lod = 0
        // } else {
        //     this.lod = islod
        // }
    },
    methods: {
        lodingFun () {
            this.lod = 1
            localStorage.setItem('spaceMenu', 1)
            this.$router.replace({name: 'frame'})
        },
        // 登陆
        access () {
            let oData = {
                passWord: this.input
            }
            this.http.access(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    // this.lod = 1
                    // sessionStorage.setItem('spaceMenu', 1)
                    this.isTokenVaildity(res.data)
                }
            })
        },
        // 判断token是否有效
        isTokenVaildity (data) {
            let oData = {
                token: data
            }
            this.http.isTokenVaildity(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    localStorage.setItem('spaceMenu', data)
                    this.$router.push({name: 'frame'})
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.frame-login{
    width:100%;
    height:100%;
    background:url('../../assets/img/frame-bg.png') no-repeat center center;
    background-size:100% 100%;
    position: absolute;
    top:0;
    left:0;
    z-index: 999;
    .logo{
        width: 150px;
        height: 50px;
        background:url('../../assets/img/frame-logo.png') no-repeat center center;
        position: absolute;
        background-size:100% 100%;
        top:40px;
        left:60px;
    }
    .container{
        width: 50%;
        height: 300px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -150px;
        margin-left: -25%;
        color:#fff;
        font-size:48px;
        text-align:center;
        letter-spacing: 5px;
        .frame-input{
            width:calc(100% - 80px);
            height:70px;
            background:url('../../assets/img/frame-dl-bg.png') no-repeat center center;
            background-size: 100% 100%;
            margin-top: 135px;
            padding:0 40px;
            .name{
                width:auto;
                height:70px;
                color:#fff;
                font-size:18px;
                line-height: 70px;
                float: left;
                letter-spacing: 1px;
            }
            .put{
                width:75%;
                height:70px;
                line-height: 48px;
                float: left;
            }
            .landing{
                width:100px;
                height:40px;
                background:url('../../assets/img/frame-dl.png') no-repeat center center;
                float: right;
                font-size: 18px;
                background-size: 100% 100%;
                margin-top: 15px;
                line-height: 40px;
                cursor: pointer;
            }
        }
    }
}
</style>



